body {
  transition: color var(--duration) var(--cubic), background-color var(--duration) var(--cubic);
  font-family: Montserrat, sans-serif;
  font-size: 15px;
}

html {
  scroll-behavior: smooth;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

:root {
  --black-text-color: #000;
  --white-text-color: #fffbf8;
  --dark-text-color: #2e2e2e;
  --light-accent-color: #00a3ff;
  --light-accent-hover-color: #0b78b6;
  --dark-accent-color: #ffc700;
  --dark-accent-hover-color: #fda700;
  --text-shadow: 1px 1px 4px #151719;
  --lightBGL: #fdfeff;
  --darkBG: #151719;
  --modal-overlay: rgba(0, 0, 0, .25);
  --cubic: cubic-bezier(.4, 0, .2, 1);
  --duration: 250ms;
  --gradient-hero-mob: linear-gradient( 23.82deg, rgba(2, 26, 47, .5) 56.92%, rgba(0, 55, 102, 0) 145.73% );
  --gradient-hero-tab: linear-gradient( 72.44deg, rgba(0, 31, 49, .63) 56.63%, rgba(255, 255, 255, 0) 100% );
  --gradient-hero-pc: linear-gradient( 72.44deg, rgba(0, 31, 49, .57) 56.63%, rgba(255, 255, 255, 0) 100% );
  --gradient-contacts: linear-gradient( 0deg, rgba(0, 0, 0, .8), rgba(0, 0, 0, .8));
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width: 480px) {
  .container {
    padding: 0 31px;
  }
}

@media screen and (min-width: 480px) and (max-width: 767px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (min-width: 1350px) {
  .container {
    width: 1260px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 1700px) {
